import { Component, Host, h, Prop, State, Listen } from '@stencil/core';
import { Memoize, isEmpty, parseIfString } from '@springbokagency/stencil-base';
import { BsTitle } from '../../atoms/title/title';
import { BsImage } from '../../atoms/image/image';
import { BsButton } from '../../atoms/button/button';
import { Variant } from './header-banner.types';
import { TitleSize, TitleTag } from '../../atoms/title/title.types';
import { Breakpoint, compareBreakpoint } from '../../../utils/breakpoints';
import { throttle } from '../../../utils/throttle';
import { BsVideoCard } from '../../molecules/video-card/video-card';

/**
 * Header banner component.
 */
@Component({
  tag: 'bs-header-banner',
  styleUrl: 'header-banner.scss',
  shadow: true,
})
export class BsHeaderBanner {
  /** States */
  @State() isMobile: boolean = compareBreakpoint('max', Breakpoint.md);

  /**
   * A (json)object string containing the bs-image elemement properties.
   */
  @Prop() image?: BsImage | string;

  @Memoize('image')
  get parsedImage(): BsImage {
    return parseIfString(this.image);
  }

  /**
   * A (json)object string containing the bs-image that is used for the logo elemement properties.
   */
  @Prop() logo?: BsImage | string;

  @Memoize('logo')
  get parsedLogo(): BsImage {
    return parseIfString(this.logo);
  }

  /** A label to be displayed on the top if there is no logo. */
  @Prop() label?: string;

  /**
   * A (json)object string containing the bs-title elemement properties.
   */
  @Prop() headerTitle: BsTitle | string;

  @Memoize('headerTitle')
  get parsedHeaderTitle(): BsTitle {
    return parseIfString(this.headerTitle);
  }

  /** The header banner subtitle. */
  @Prop() subtitle?: string;

  /**
   * A (json)object string containing the bs-button elemement properties.
   */
  @Prop() button?: BsButton | string;

  @Memoize('button')
  get parsedButton(): BsButton {
    return parseIfString(this.button);
  }

  /** The header banner variant. Either 'image' or 'video'. */
  @Prop() variant: Variant;

  /**
   * A (json)object string containing the bs-video-card elemement properties.
   */
  @Prop() video?: BsVideoCard | string;

  @Memoize('video')
  get parsedvideo(): BsVideoCard {
    return parseIfString(this.video);
  }

  /** Listeners */
  @Listen('resize', { target: 'window' })
  handleScreenResized() {
    this.throttleMethod();
  }

  /** Methods */
  private onResize = () => {
    this.isMobile = compareBreakpoint('max', Breakpoint.md);
  };

  private throttleMethod = throttle(this.onResize, 1000);

  render() {
    return (
      <Host>
        <header class="header-banner">
          <div class="content">
            {!isEmpty(this.parsedLogo) ? (
              this.parsedLogo.src && <bs-image class="logo" {...this.parsedLogo} />
            ) : (
              <bs-title
                size={TitleSize.Head2}
                tag={TitleTag.Span}
                class="label"
                text={this.label}
              />
            )}
            <div class="center">
              <bs-title class="title" {...this.parsedHeaderTitle} />
              {this.subtitle && <bs-text class="sub" text={this.subtitle} />}
              {this.parsedButton && this.parsedButton.text && (
                <bs-button class="button" {...this.parsedButton} isFullWidth={!!this.isMobile} />
              )}
            </div>
          </div>
          <div class="image">
            {this.variant === Variant.Image ? (
              <bs-image {...this.parsedImage} caption="" />
            ) : (
              <bs-video-card {...this.parsedvideo} />
            )}
          </div>
        </header>
      </Host>
    );
  }
}
