:host {
  @include reset-host;
  overflow: hidden;
}

:host .header-banner {
  display: flex;
  flex-direction: column;
  gap: rem(24);
  background-color: $light-gray;

  @include mq('min', 'md') {
    flex-direction: row;
    gap: 0;
    height: rem(425);
  }
}

:host .content {
  position: relative;
  display: flex;
  flex: 1;
  flex-direction: column;
  gap: rem(16);
  padding: rem(40) rem(24) 0;

  @include mq('min', 'md') {
    gap: 0;
    padding: rem(40);
    padding-right: 0;
    margin-right: 1px;

    &:before {
      @include z-index('base');
      position: absolute;
      top: 0;
      right: 0;
      width: 3px;
      height: 100%;
      content: '';
      border-top: 425px solid $light-gray;
      border-right: 99px solid transparent;
      transform: translateX(100%);
    }

    &:after {
      @include z-index('base');
      position: absolute;
      top: 0;
      right: 0;
      width: 2px;
      height: 100%;
      content: '';
      background-color: $red-50;
      transform: skew(-13deg) translateX(1px);
      transform-origin: right bottom;
    }
  }
}

:host .image {
  position: relative;
  flex: 2;
  width: 100%;
  aspect-ratio: 16/9;

  @include mq('min', 'md') {
    --image--height: 425px;
    width: 756px;
    max-width: 756px;
    aspect-ratio: auto;
  }
}

:host .logo {
  --image--max-width: 180px;
  --image--max-height: 60px;
  margin-bottom: rem(8);
}

:host .label {
  margin-bottom: rem(8);

  @include mq('min', 'md') {
    margin-bottom: rem(42);
  }
}

:host .title + .button {
  margin-top: rem(20);
}

:host .center {
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: center;
}
